@mixin rich-media-hero__icon {
  @include transition(transform);
  @include perspective(1200px, horizontal, 'svg');

  svg {
    width: $lem * 5;
    transform: rotateX(70deg);
  }

  @include rich-media-hero__icon--mobile;
  @include rich-media-hero__icon--tablet;
  @include rich-media-hero__icon--landscape;
}
