// Resize elements to fit the ipad mockup
//
// - the original ipad mockup is 2185x1299px and the rectangle to fill inside is 1188x366px
// - we should keep the above proportions with these constants:
// - 54.37 = 1188 * 100 / 2185
// - 3.2459 = 1188 / 366
//
// This function resizes what goes into the ipad according to the proportions from above
// - the resize is responsive based on the different ipad mockup sizes
//


@function rich-media-hero-resize-width($width, $padding) {
  $w: $width * 54.37 / 100;
  @return calc(#{$w}px - #{$padding});
}

@function rich-media-hero-resize-height($width, $padding) {
  $h: $width / 3.2459;
  @return calc(#{$h}px - #{$padding});
}



@mixin rich-media-hero--resize($padding) {
  max-width: none;
  max-height: none;

  @include mobile {
    width: rich-media-hero-resize-width(300, $padding / 3);
    height: rich-media-hero-resize-height(300, $padding / 3)
  }

  @include tablet {
    width: rich-media-hero-resize-width(768, $padding / 1.55);
    height: rich-media-hero-resize-height(768, $padding / 1.4)
  }

  @include laptop {
    width: rich-media-hero-resize-width(1093, $padding);
    height: rich-media-hero-resize-height(1093, $padding)
  }

  @include desktop {
    width: rich-media-hero-resize-width(1093, $padding);
    height: rich-media-hero-resize-height(1093, $padding)
  }
}
