@mixin rich-media-hero__specs {
  @include transition(transform);

  .text {
    @include p;
    @include text-style(headline-3);
    text-align: center;

    @include rich-media-hero--resize($lem * 2 + $lem / 4); // padding + border
    @include border(thin, all);
    padding: $lem;
    box-sizing: border-box;
  }

  @include rich-media-hero__specs--mobile;
  @include rich-media-hero__specs--tablet;
  @include rich-media-hero__specs--landscape;
}
