@mixin rich-media {
  @include colors(white-on-green);

  @include section-fitscreen;

  display: flex;
  flex-direction: column;

  &__headline {
    @include rich-media__headline;
  }

  .rich-media-hero {
    @include rich-media-hero;
  }

  .articles {
    @include articles;
    @include section-margin;

    .article {
      &__subtitle {
        padding: $lem / 4 0;
      }
    }
  }
}
