// Setting the distance between elements
@mixin rich-media-hero--grid {
  position: relative;

  .rich-media-hero__icon,
  .rich-media-hero__specs,
  .rich-media-hero__image {
    position: relative;
    height: $lem * 3;
  }

  .rich-media-hero__image {
    z-index: 2;
  }

  .rich-media-hero__icon {
    z-index: 3;
  }

  .rich-media-hero__video {
    position: relative;
    z-index: 4;
    height: 0;
  }
}
