@mixin rich-media-hero__specs--mobile {
  @include mobile {
    @include rich-media-hero--reposition(250px, 6.7em, $lem / 16, '.text');

    .text {
      font-size: 1em;
      padding: $lem / 2;

      p {
        display: none;

        &:first-of-type {
          display: flex;
        }
      }
    }
  }
}
