@mixin rich-media-hero {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include rich-media-hero--grid;

  &__icon {
    @include rich-media-hero__icon;
  }

  &__image {
    @include rich-media-hero__image;
  }

  &__specs {
    @include rich-media-hero__specs;
  }

  &__ipad {
    @include rich-media-hero__ipad;
  }

  &__video {
    @include rich-media-hero__video;
  }
}
