// - https://css-tricks.com/almanac/properties/p/perspective/
@mixin perspective($angle, $direction, $children) {
  perspective: $angle;

  #{$children} {
    @if ($direction == horizontal) {
      transform: rotateX(50deg);
    }

    @if ($direction == vertical) {
      transform: rotateY(50deg);
    }
  }
}
